.team {
    background: url("../img/bg3.jpg") 50% no-repeat;
    background-size: cover;
    position: relative;

    display: flex;
    flex-direction: column;
}

.team__area {

    position: absolute;
    right: 0;
    top: 0;
    bottom: 0;
    width: 50%;
    background: rgba(#2f3234, .93);
    padding: 5% 2% 0 7%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    @include tablets {
        width: 100%;
        padding: 0; 
        margin: 0 auto;
        text-align: center;
    }
}

.title__team {
    color: $yellow;
    margin-bottom: 12%;
    
}

.team__item {

    margin: 20px 0;
    
}

.team__item.active .team__icon {
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    transform: rotate(180deg);
}

.team__icon {
    display: inline;


}
.team__link {
    font-family: Gagalin;
    color: #fff;
    font-size: rem(14px);
    text-decoration: none;
    position: relative;
    display: inline;

    //display: block;
    width: 100%;
    height: 40px;
    transition: 0.4s;

    margin-bottom: rem(20px);
    align-items: center;
    

    @include tablets() {
        font-size: rem(25px);
    }
}
.member__member-wrapper {
    
    height: 0;
    transition: height 0.4s;
    
}
.team__member {
    display: flex;
    color: white;
    font-weight: 300;
    font-family: Ubuntu;
    overflow: hidden;
    height: 100%;
    width: 100%;
    margin-top: rem(14px);
    // transition: height 0.4s;

    @include tablets {
        flex-direction: column;
        align-items: center;        
    }
}

.member__description {
    width: 70%;
    font-family: "Ubuntu";
    font-size: rem(14px);
    color: white;
    margin-left: rem(15px);
    text-align: left;
    @include tablets {
        margin: initial;
        text-align: center;
    }
}

.member__status {
    display: block;
    font-weight: bold;
    font-size: rem(14px);
    color: #fff;
    margin-bottom: rem(14px);
}

.team__icon {
    display: inline-block; // vertical-align: middle;
    fill: $yellow;
    width: rem(10px);
    height: rem(10px);
    transition: 0.3s;
}

.member__img {
    width: rem(100px);
    height: rem(85px);

    text-align: center;

    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    border-radius: 50%;
}
.member__text {

    color: #fff;
    font-size: rem(14px);
}
